.header {
    background: #0d0d0d;
    padding: 1.5rem 0;

    margin-bottom: 4rem;

    @include breakpoint($tablet) {
        padding: 2rem 0;
    }

    &.active {
        width: 100%;
        height: 100vh;
    }
}

.burger {
    display: none;
    width: 25px;
    height: 25px;
    img {
        filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(326deg) brightness(104%) contrast(102%);
    }

    @include breakpoint($tablet) {
        display: block;
    }

    &.closed {
        display: none;
    }
}