.nav {
    position: relative;
    @include breakpoint($tablet) {
        display: none;
    }

    &.active {
        display: block;
    }
}

.nav__inner {
    display: flex;
    justify-content: space-between;

    @include breakpoint($tablet) {
        flex-direction: column;
        justify-content: center;
        align-items: center;

        padding-top: 5rem;
    }
}

.nav__close {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 25px;
    height: 25px;
    img {
        width: 100%;
        filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(326deg) brightness(104%) contrast(102%);
    }

    display: none;

    &.active {
        display: block;
    }
}

.nav__item {
    font-size: 1.5rem;
    color: $white;
    font-weight: 600;

    a {
        color: inherit;
        text-decoration: none;
    }

    @include breakpoint($tablet) {
        font-size: 2.5rem;
        margin-bottom: 3rem;
    }
}