.footer {
    background: #0d0d0d;
    padding: 1.5rem 0;
}

.footer__inner {
    display: flex;

    align-items: center;

    @include breakpoint($mobile) {
        flex-direction: column;
    }
}

.footer__text {
    width: 70%;
    font-size: 1.7rem;
    color: $white;
    font-weight: 400;

    @include breakpoint($mobile) {
        width: 100%;
    }
}

.footer__nav {
    width: 30%;

    @include breakpoint($mobile) {
        width: 100%;
    }

}

.footer__nav-inner {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @include breakpoint($mobile) {
        align-items: center;
    }
}

.footer__nav-item {
    font-size: 1.6rem;
    color: $white;
    font-weight: 500;

    margin-bottom: 0.5rem;

    a {
        color: inherit;
        text-decoration: none;
    }

    @include breakpoint($mobile) {
        font-size: 1.8rem;
    }
}